___CSS_LOADER_EXPORT___.push([module.id, `.cover {
    --rise-time: 1.5s;
    height: 100vh;
    height: 100dvh;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.title {
    position: sticky;
    top: 120px;
    width: 100%;

    background-image: 
        linear-gradient(
            to right, 
            var(--clr-title-1),
            var(--clr-title-2)
        );

    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

    font-size: 7.5rem;
    font-family: 'Quicksand';
    text-align: center;
    text-shadow: -2px -2px var(--clr-title-shadow);
    letter-spacing: .25rem;

    animation: 
        rise-show 
        var(--rise-time) 
        ease-out 
        1 
        forwards;

    z-index: 1;

    &:hover {
        background-image: 
        linear-gradient(
            to right, 
            var(--clr-title-2),
            var(--clr-title-3)
        );
    }
}

.search {
    position: sticky;
    top: min(300px, 35vh);

    border-radius: 50%;
    overflow: hidden;

    backdrop-filter: blur(20px);
    opacity: .9;

    transition: border-radius .5s .2s;

    animation: 
        rise 
        var(--rise-time) 
        ease-out 
        1 
        forwards;
    
    z-index: 2;
    
    &:focus-within, 
    &:hover {
        transition: border-radius .1s;
        border-radius: 1rem;
        box-shadow: 0 0 5px var(--clr-search-button);
        opacity: 1;
    }
}

@keyframes rise {
    from {translate: 0 25vh;}
}

.search form {
    display: flex;
    align-items: center;
}

.search-input {
    width: 0;
    height: 3rem;

    font-size: 1.2rem;
    text-indent: 1.2rem;
    background-color: #fff;
    color: var(--clr-primary);
    outline: none;
    
    transition: .5s;
    
    &:focus,
    .search:hover & {
        width: clamp(12rem, 8rem + 10vw, 15rem);
        transition: .25s;
    }
    
    &::placeholder {
        color: var(--clr-search-button-hover);
    }
}

.search form button {
    width: 3rem;
    aspect-ratio: 1 / 1;

    display: grid;
    place-items: center;

    font-size: 1.2rem;
    background-color: var(--clr-search-button);
    cursor: pointer;
    
    transition: background-color 0.2s;
    
    .search:hover &,
    .search:focus-within & {
        background-color: var(--clr-search-button-hover);
    }
    
    & .icon {
        color: var(--clr-btn);
        animation: 
            rise-show
            var(--rise-time)
            ease-out 
            1 
            forwards;
    }
}

.search + .hint {
    --opacity: .75;

    position: sticky;
    top: min(355px, calc(35vh + 3.5rem));
    
    color: var(--clr-search-button-hover);
    opacity: 0;
    font-size: .75rem;
    text-align: center;
    
    transition: opacity .3s;
    
    animation: 
        rise-show
        var(--rise-time)
        ease-out 
        1 
        forwards;
    animation-delay: .2s;
    
    z-index: 2;
     
    .search:hover + &,
    .search:focus-within + & {
        opacity: 0 !important;
    }
}

@keyframes rise-show {
    0%, 80% {opacity: 0;}
    100% {opacity: var(--opacity, 1);}
}`, "",{"version":3,"sources":["webpack://./components/Cover.css"],"names":[],"mappings":"AAAA;IACI,iBAAiB;IACjB,aAAa;IACb,cAAc;;IAEd,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;IAChB,UAAU;IACV,WAAW;;IAEX;;;;;SAKK;;IAEL,qBAAqB;IACrB,6BAA6B;IAC7B,oCAAoC;;IAEpC,iBAAiB;IACjB,wBAAwB;IACxB,kBAAkB;IAClB,8CAA8C;IAC9C,sBAAsB;;IAEtB;;;;;gBAKY;;IAEZ,UAAU;;IAEV;QACI;;;;;SAKC;IACL;AACJ;;AAEA;IACI,gBAAgB;IAChB,qBAAqB;;IAErB,kBAAkB;IAClB,gBAAgB;;IAEhB,2BAA2B;IAC3B,WAAW;;IAEX,iCAAiC;;IAEjC;;;;;gBAKY;;IAEZ,UAAU;;IAEV;;QAEI,6BAA6B;QAC7B,mBAAmB;QACnB,4CAA4C;QAC5C,UAAU;IACd;AACJ;;AAEA;IACI,MAAM,iBAAiB,CAAC;AAC5B;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,QAAQ;IACR,YAAY;;IAEZ,iBAAiB;IACjB,mBAAmB;IACnB,sBAAsB;IACtB,yBAAyB;IACzB,aAAa;;IAEb,eAAe;;IAEf;;QAEI,uCAAuC;QACvC,gBAAgB;IACpB;;IAEA;QACI,qCAAqC;IACzC;AACJ;;AAEA;IACI,WAAW;IACX,mBAAmB;;IAEnB,aAAa;IACb,mBAAmB;;IAEnB,iBAAiB;IACjB,0CAA0C;IAC1C,eAAe;;IAEf,iCAAiC;;IAEjC;;QAEI,gDAAgD;IACpD;;IAEA;QACI,qBAAqB;QACrB;;;;;oBAKY;IAChB;AACJ;;AAEA;IACI,cAAc;;IAEd,gBAAgB;IAChB,oCAAoC;;IAEpC,qCAAqC;IACrC,UAAU;IACV,iBAAiB;IACjB,kBAAkB;;IAElB,uBAAuB;;IAEvB;;;;;gBAKY;IACZ,oBAAoB;;IAEpB,UAAU;;IAEV;;QAEI,qBAAqB;IACzB;AACJ;;AAEA;IACI,SAAS,UAAU,CAAC;IACpB,MAAM,0BAA0B,CAAC;AACrC","sourcesContent":[".cover {\n    --rise-time: 1.5s;\n    height: 100vh;\n    height: 100dvh;\n\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\n.title {\n    position: sticky;\n    top: 120px;\n    width: 100%;\n\n    background-image: \n        linear-gradient(\n            to right, \n            var(--clr-title-1),\n            var(--clr-title-2)\n        );\n\n    background-clip: text;\n    -webkit-background-clip: text;\n    -webkit-text-fill-color: transparent;\n\n    font-size: 7.5rem;\n    font-family: 'Quicksand';\n    text-align: center;\n    text-shadow: -2px -2px var(--clr-title-shadow);\n    letter-spacing: .25rem;\n\n    animation: \n        rise-show \n        var(--rise-time) \n        ease-out \n        1 \n        forwar